* {
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #777;
  background-color: rgba(224, 221, 221, 0.096);
  letter-spacing: 0.25px;
}
.nav-bar {
  z-index: 1;
}

.account_circle {
  margin-right: 50px;
}

.footer-list ul {
  display: flex;
  justify-content: center;
}

.footer-list ul li a i {
  font-size: 1.5em;
  padding: 20px;
}

.rights {
  font-size: 12px;
}

.showcase {
  background-image: url(/praxprofile/static/media/bg4.199e9ec1.jpg);
  background-position: cover;
  height: 75vh;
  margin-top: -30px;
  margin-bottom: 5px;
}

.showcase-content {
  vertical-align: top;
}

.showcase-header {
  text-transform: uppercase;
  padding-top: 15%;
}

.head-01 {
  display: block;
  letter-spacing: 10%;
  font-size: 50px;
  font-weight: 400;
}

.head-02 {
  display: block;
  letter-spacing: 18.2px;
  font-size: 20px;
  font-weight: 300;
}

.p-text {
  font-size: 24px;
  font-weight: 300;
  padding: 10px;
}

.section-heading {
  margin-bottom: 10px;
  padding: 15px;
}

.images-cert {
  display: flex;
  width: 90%;
  margin-bottom: 10px;
}

.card-heading {
  text-align: center;
  padding: 10px 0px;
}

a.logo {
  padding-left: 3.5%;
  letter-spacing: 0.12rem;
}

#nav-mobile {
  margin-top: 68px;
}

.side-menu {
  z-index: 1;
}

